import Vue from 'vue'
import Vuex from 'vuex'
import chart from './modules/chart'
import permission from './modules/permission'
import getters from './getters'

Vue.use(Vuex)

const store = new Vuex.Store({
  modules: {
    chart,
    permission
  },
  getters
})

export default store



