  import {
    ajaxPost
  } from '@/libs/utils.js'
  import {
    projectName
  } from '../../libs/project'
  import _ from 'lodash'
  const formatCityList = (data, formatData) => {
    return formatData.map(item => {
      const tem = _.filter(data, _.matches({
        parentId: item.id,
        showCity: '1'
      }));
      const child = tem.map(e => {
        return {
          label: item.cityName + '-' + e.cityName,
          value: (e.id + '')
        }
      });
      child.unshift({
        label: item.cityName,
        value: (item.id + '')
      });
      return {
        label: item.cityName,
        value: (item.id + ''),
        isS: false,
        count: 0,
        chooseCity: [],
        allCity: false,
        children: child
      }
    })
  }
  const data = {
    state: {
      cityList: []
    },
    actions: {
      getCityListData({
        commit,
        state
      }) {
        // ajaxPost(projectName + '/mgmt/city/findAll.do').then(res => {
        //   const formatData = _.filter(res, _.matches({
        //     parentId: 0,
        //     showCity: '1'
        //   }));
        //   state.cityList.splice(0, state.cityList.length, ...formatCityList(res, formatData));
        // })
      }
    }
  };

  export default data;



