const Login = r => require.ensure([], () => r(require('@/views/login/login')), 'main')
const CommonLayout = r => require.ensure([], () => r(require('@/layouts/common')), 'main')
const NothingLayout = r => require.ensure([], () => r(require('@/layouts/nothing')), 'main')

const IndexAdmin = r => require.ensure([], () => r(require('@/views/index-admin/index')), 'indexadmin')
const AreaAdmin = r => require.ensure([], () => r(require('@/views/index-admin/area')), 'indexadmin')
const ClassifyAdmin = r => require.ensure([], () => r(require('@/views/index-admin/classify')), 'indexadmin')
const InstitutionAdmin = r => require.ensure([], () => r(require('@/views/index-admin/institution')), 'indexadmin')
const YearAdmin = r => require.ensure([], () => r(require('@/views/index-admin/year')), 'indexadmin')
const SourceAdmin = r => require.ensure([], () => r(require('@/views/index-admin/source')), 'indexadmin')

const Data = r => require.ensure([], () => r(require('@/views/data/index')), 'data')
const entering = r => require.ensure([], () => r(require('@/views/data/entering')), 'data')

const User = r => require.ensure([], () => r(require('@/views/user/index')), 'user')
const CommonUser = r => require.ensure([], () => r(require('@/views/user/user')), 'user')
const userInfo = r => require.ensure([], () => r(require('@/views/user/info')), 'user')

const News = r => require.ensure([], () => r(require('@/views/news/index')), 'news')
const Conmbox = r => require.ensure([], () => r(require('@/views/news/combox')), 'news')

const Topic = r => require.ensure([], () => r(require('@/views/topic/index')), 'topic')
const TopicConmbox = r => require.ensure([], () => r(require('@/views/topic/combox')), 'topic')

const About = r => require.ensure([], () => r(require('@/views/about/index')), 'about')

const error404 = r => require.ensure([], () => r(require('@/views/error-page/404')), 'errorpage')


export const LoginRouter = {
  path: '/login',
  name: 'login',
  component: Login,
  adminFlag: true
}

export const MainRouter = {
  path: '/',
  name: 'layout',
  component: CommonLayout,
  redirect: "/info",
  adminFlag: true,
  children: [{
      path: '/info',
      name: '个人中心',
      component: userInfo,
      adminFlag: false,
      adminShow: true
    },
    {
      path: "/adminIndex",
      name: '指标管理模块',
      title: "指标管理模块",
      icon: "document-text",
      redirect: "/adminIndex/year",
      component: NothingLayout,
      activeName: 'adminIndex',
      adminFlag: true,
      adminShow: true,
      commonShow: false,
      children: [{
        path: "/adminIndex/year",
        name: '时间管理',
        title: "时间管理",
        component: YearAdmin,
        activeName: 'adminIndex',
        meta: {
          parent: '指标体系管理'
        },
        adminFlag: true,
        adminShow: true,
        commonShow: false
      }, {
        path: "/adminIndex/source",
        name: '数据来源管理',
        title: "数据来源管理",
        component: SourceAdmin,
        activeName: 'adminIndex',
        meta: {
          parent: '指标体系管理'
        },
        adminFlag: true,
        adminShow: true,
        commonShow: false
      }, {
        path: "/adminIndex/classify",
        name: '分类管理',
        title: "分类管理",
        component: ClassifyAdmin,
        activeName: 'adminIndex',
        meta: {
          parent: '指标体系管理'
        },
        adminFlag: true,
        adminShow: true,
        commonShow: false
      }, {
        path: "/adminIndex/institution",
        name: '单位管理',
        title: "单位管理",
        component: InstitutionAdmin,
        activeName: 'adminIndex',
        meta: {
          parent: '指标体系管理'
        },
        adminFlag: true,
        adminShow: true,
        commonShow: false
      }, {
        path: "/adminIndex/index",
        name: '指标管理',
        title: "指标管理",
        component: IndexAdmin,
        activeName: 'adminIndex',
        meta: {
          parent: '指标体系管理'
        },
        adminFlag: true,
        adminShow: true,
        commonShow: false
      }]
    },
    {
      path: "/data",
      name: '数据管理模块',
      title: "数据管理模块",
      icon: "stats-bars",
      redirect: "/data/index",
      component: NothingLayout,
      activeName: 'data',
      children: [{
          path: "/data/index",
          name: '指标数据',
          title: "指标数据",
          component: Data,
          activeName: 'data',
          meta: {
            parent: '数据管理'
          },
          adminFlag: true,
          adminShow: false,
          commonShow: true
        },
        // {
        //   path: "/data/entering",
        //   name: '录入进度',
        //   title: "录入进度",
        //   component: entering,
        //   activeName: 'data',
        //   meta: {
        //     parent: '数据管理'
        //   },
        //   adminFlag: true
        // }
      ],
      adminFlag: true,
      adminShow: false,
      commonShow: true
    },
    {
      path: "/user",
      name: '用户管理模块',
      title: "用户管理模块",
      icon: "person",
      component: NothingLayout,
      redirect: "/user/index",
      activeName: 'user',
      adminFlag: true,
      adminShow: false,
      commonShow: false,
      children: [{
        path: "/user/index",
        name: '管理员账号管理',
        title: "管理员账号管理",
        component: User,
        activeName: 'user',
        meta: {
          parent: '用户管理'
        },
        commonShow: false,
        adminShow: false,
        adminFlag: true,
      }, {
        path: "/user/commonuser",
        name: '普通用户管理',
        title: "普通用户管理",
        component: CommonUser,
        activeName: 'user',
        meta: {
          parent: '用户管理'
        },
        commonShow: false,
        adminShow: false,
        adminFlag: true
      }]
    },
    {
      path: "/news",
      name: '新闻模块',
      title: "新闻模块",
      icon: "social-designernews",
      component: NothingLayout,
      redirect: "/news/index",
      activeName: 'news',
      adminShow: true,
      adminFlag: true,
      commonShow: false,
      children: [{
          path: "/news/index",
          name: '新闻管理',
          title: "新闻管理",
          component: News,
          activeName: 'news',
          meta: {
            parent: '新闻管理'
          },
          adminShow: true,
          commonShow: false,
          adminFlag: true
        },
        // {
        //   path: "/news/conmbox",
        //   name: '新闻分类',
        //   title: "新闻分类",
        //   component: Conmbox,
        //   activeName: 'news',
        //   adminFlag: true,
        //   adminShow: true,
        //   meta: {
        //     parent: '新闻管理'
        //   },
        //   adminShow: true,
        //   adminFlag: true
        // }
      ]
    },
    {
      path: "/topic",
      name: '专题模块',
      title: "专题模块",
      icon: "ios-star",
      component: NothingLayout,
      redirect: "/topic/index",
      activeName: 'topic',
      adminShow: true,
      commonShow: false,
      adminFlag: true,
      children: [{
          path: "/topic/index",
          name: '专题管理',
          title: "专题管理",
          component: Topic,
          activeName: 'topic',
          meta: {
            parent: '专题管理'
          },
          adminShow: true,
          commonShow: false,
          adminFlag: true
        },
        // {
        //   path: "/topic/conmbox",
        //   name: '专题分类',
        //   title: "专题分类",
        //   component: TopicConmbox,
        //   activeName: 'topic',
        //   adminFlag: true,
        //   adminShow: true,
        //   meta: {
        //     parent: '专题管理'
        //   },
        //   adminShow: true,
        //   adminFlag: true
        // }
      ]
    },
    {
      path: "/about",
      name: '指标体系模块',
      title: "指标体系模块",
      icon: "university",
      component: NothingLayout,
      redirect: "/about/index",
      activeName: 'about',
      adminFlag: true,
      commonShow: false,
      adminShow: true,
      children: [{
        path: "/about/index",
        name: '指标体系说明',
        title: "指标体系说明",
        component: About,
        activeName: 'about',
        adminFlag: true,
        adminShow: true,
        commonShow: false,
        meta: {
          parent: '指标体系模块'
        }
      }]
    },
    // {
    //   path: "/qa",
    //   name: '问卷管理',
    //   title: "问卷管理",
    //   icon: "document-text",
    //   component: NothingLayout,
    //   redirect: "/qa/index",
    //   activeName: 'qa',
    //   children: [{
    //     path: "/qa/index",
    //     name: '我的问卷',
    //     title: "我的问卷",
    //     component: resolve => {
    //       require(['@/views/qa/index1'], resolve);
    //     },
    //     activeName: 'qa',
    //     meta: {
    //       parent: '问卷管理'
    //     }
    //   }]
    // }
  ]
}

export const Error404 = {
  path: '/*',
  name: '404',
  component: error404,
  adminFlag: true
}

export const routes = [
  LoginRouter,
  MainRouter,
  Error404
]



