import Vue from 'vue'
import Router from 'vue-router'
import iView from 'iview';
import {
  title,
  forbidPath
} from '../libs/utils';
import Cookies from 'js-cookie';
import {
  routes
} from './router'
import store from '../store'

Vue.use(Router)

const RouterConfig = {
  // mode: 'history',
  routes
};

export const router = new Router(RouterConfig)

router.beforeEach((to, from, next) => {
  iView.LoadingBar.start();
  title(to.meta.title);
  const isLogin = Cookies.get('Temp-User')
  const AdminFlag = Cookies.get('administratorFlag')
  // console.log(isLogin)
  if (!!isLogin) { // 已经登录 /* has admin */
    // 判断显示对应导航栏
    if (!!AdminFlag) {
      store.dispatch('update_menulist')
    }
    // 路由指向
    (to.path === '/login') ? next({
      path: '/'
    }): next()
    iView.LoadingBar.finish()
  } else { /* has no admin */
    (to.path === '/login') ? next(): next({
      path: '/login'
    })
    iView.LoadingBar.finish()
  }
});

router.afterEach((to) => {
  iView.LoadingBar.finish();
  window.scrollTo(0, 0);
});



