import Vue from 'vue'
import {
  Message,
  Modal
} from 'iview';
import axios from 'axios'
import qs from 'qs'
import Cookies from 'js-cookie'
import {
  router
} from '../router/index'
import {
  locker
} from './project'

const title = (title) => {
  window.document.title = title || '上海学前教育监测数据系统平台';
};

// 建议不要直接调用axios,采用创建实例的方式
// 集中处理异常情况
const instance = axios.create({
  timeout: 15000 // 请求超时时间
})
const method = (type, api, params) => {
  return new Promise((resolve, reject) => {
    instance[type](api, params).then(res => {
      if ('status' in res.data) {
        if (res.status === 200 && res.data.status && res.data.status === 2000) {
          resolve(res.data.message);
        } else if (res.data.status === 4401) {
          Modal.confirm({
            title: '提示',
            content: '<p>用户过期，重新登录</p>',
            loading: true,
            onOk: () => {
              Modal.remove()
              Cookies.remove('Temp-User')
              Cookies.remove('administratorFlag')
              Cookies.remove('U-ID')
              router.replace('/login')
            }
          })
        } else if (res.data.status === 4011) {

        } else {
          Message.warning(res.data.message);
        }
      } else {
        resolve(res.data);
      }
    }).catch((err) => {
      Message.warning('服务器繁忙');
      reject(err)
    })
  })
}

const ajaxGet = (api, data = null) => {
  const params = Object.assign({}, data, locker);
  return method('get', api, {
    params: qs.stringify(params)
  })
}

const ajaxPost = (api, data = null) => {
  const params = Object.assign({}, data, locker);
  return method('post', api, qs.stringify(params))
}

const adminFlagPath = ['/login', '/', '/info', '/adminIndex', '/adminIndex/index', '/data', '/data/index']

const forbidPath = (to) => {
  return adminFlagPath.includes(to.path);
}

export {
  title,
  ajaxGet,
  ajaxPost,
  forbidPath
}



