// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import {
  router
} from './router/index'
import iview from 'iview'
import {
  Button
} from 'iview'
import 'iview/dist/styles/iview.css'
import {
  ajaxGet,
  ajaxPost
} from '@/libs/utils.js'

import {
  projectName,
  locker
} from '@/libs/project.js'
import echarts from 'echarts'

import VueKindEditor from 'vue-kindeditor'
import 'kindeditor/kindeditor-all-min.js'
import 'kindeditor/themes/default/default.css'

import cascaderMulti from "./components/cascader";

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

Vue.use(cascaderMulti);

Vue.use(iview);
Vue.use(VueKindEditor)
Vue.config.productionTip = false

Vue.prototype.$echarts = echarts
Vue.prototype.$ajaxGet = ajaxGet
Vue.prototype.$ajaxPost = ajaxPost
Vue.prototype.projectName = projectName
Vue.prototype.cityList = store.state.chart.cityList;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



