import {
  MainRouter
} from "../../router/router"
import Cookies from 'js-cookie'
const permission = {
  state: {
    menus: []
  },
  mutations: {
    UPDATE_MENULIST: state => {
      let menuList = JSON.parse(JSON.stringify(MainRouter.children)) // menuList和MainRouter.children指向的是同一个内存地址,用JSON.parse(JSON.stringify())处理
      let showList = menuList.filter(item => item.adminFlag)
      let showList1 = menuList.filter(item => !item.adminShow)
      if (JSON.parse(Cookies.get('Temp-User')).roleKey === 'admin' && JSON.parse(Cookies.get('Temp-User')).name === 'admin') {
        state.menus = showList
      } else if (JSON.parse(Cookies.get('Temp-User')).roleKey !== 'admin') {
        state.menus = showList.filter(item => item.commonShow)
      } else {
        showList1.forEach(el => {
          el.children = el.children.filter(i => !i.adminShow)
        })
        state.menus = showList1
      }
    }
  },
  actions: {
    update_menulist({
      commit
    }) {
      commit('UPDATE_MENULIST')
    }
  }
}

export default permission



